import React, { useState, useContext, useEffect } from 'react'
import axios from 'axios';
import baseURL from '../helpers/config';


const GeneralContext = React.createContext()

export function useGenCtx () {
	return useContext(GeneralContext)
}

const GeneralProvider = ({ children }) => {
	const [fetchError, setError] = useState(false);
	const [categories, setCategories] = useState([]);
	const [loadingCategories, setLoading] = useState(false);
	
	//creating category vars
	const [creatingCat, setCreatingCat] = useState(false);
	const [creatingCatError, setCatError] = useState(null);



	const FetchCategories = async () => {
			setLoading(true);
			setError(null);
			try {
				const dt = await axios.get(baseURL+'/article-category');
				const { data } = dt.data;
				setError(null);
        setCategories(data)
				console.log(data);
				setLoading(false);
			} catch (error) {
				setError(error.message);
				setLoading(false);
			}
	};

	const CreateCategory = async (name) => {
			setCreatingCat(true);
			setCatError(null);
			try {
				const dt = await axios.post(baseURL+'/article-category/create', {category: name});
				const { category } = dt.data;
				setCatError(null);
				setCategories(prev => [...prev, category])
				setCreatingCat(false);
			} catch (error) {
				setCatError(error.message);
				setCreatingCat(false);
			}
	};


	useEffect(() => {
FetchCategories()
	}, [])


	return (
		<GeneralContext.Provider
			value={{
        loadingCategories,
        categories,
        fetchError,
        FetchCategories,

				CreateCategory,
				creatingCat,
				creatingCatError,

		


			}}
		>
			{children}
		</GeneralContext.Provider>
	);
}
export default GeneralProvider
