import { Box, Center, CircularProgress, SimpleGrid } from '@chakra-ui/react';
import { useEffect } from 'react';
import { useState } from 'react';
import PageTitle from '../components/Global/PageTitle';
import PostCard from '../components/ListPosts/PostCard';
import categories from '../helpers/categories';
import testPosts from '../helpers/posts';

function ListPosts(props) {
  const [loadingPosts, setLoadingPosts] = useState(false);
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    setLoadingPosts(true);
    setTimeout(() => {
      setLoadingPosts(false);
      const { data } = testPosts;
      setPosts(data);
    }, 2500);
  }, []);

  return (
    <Box pb="50px">
      <PageTitle category={categories.blog} title="All Posts" />
      <SimpleGrid spacing="30px" columns={[1, 1, 2, 3]}>
        {!loadingPosts &&
          posts &&
          posts.map((post, index) => {
            return <PostCard post={post} key={index} />;
          })}
      </SimpleGrid>
      {loadingPosts && (
        <Center>
          <CircularProgress isIndeterminate color="green.300" size="100px" />
        </Center>
      )}
    </Box>
  );
}

export default ListPosts;
